                   0 ${this.pixelSize * -1.5}px 0 0\r\n                      `\r\n        }\r\n      }\r\n    },\r\n\r\n    watch: {\r\n      size: {\r\n        handler: 'updateAnimation',\r\n        immediate: true\r\n      }\r\n    },\r\n\r\n    mounted () {\r\n      this.updateAnimation()\r\n    },\r\n\r\n    beforeDestroy () {\r\n      utils.removeKeyframes(this.animationName)\r\n    },\r\n\r\n    methods: {\r\n      updateAnimation () {\r\n        utils.removeKeyframes(this.animationName)\r\n        utils.appendKeyframes(this.animationName, this.generateKeyframes())\r\n      },\r\n\r\n      generateKeyframes () {\r\n        return `\r\n        50% {\r\n          box-shadow:  ${this.pixelSize * 2}px ${this.pixelSize * 2}px 0 0,\r\n                       ${this.pixelSize * -2}px ${this.pixelSize * -2}px 0 0,\r\n                       ${this.pixelSize * 2}px ${this.pixelSize * -2}px 0 0,\r\n                       ${this.pixelSize * -2}px ${this.pixelSize * 2}px 0 0,\r\n                       0 ${this.pixelSize}px 0 0,\r\n                       ${this.pixelSize}px 0 0 0,\r\n                       ${this.pixelSize * -1}px 0 0 0,\r\n                       0 ${this.pixelSize * -1}px 0 0;\r\n        }\r\n\r\n\r\n        75% {\r\n          box-shadow:  ${this.pixelSize * 2}px ${this.pixelSize * 2}px 0 0,\r\n                       ${this.pixelSize * -2}px ${this.pixelSize * -2}px 0 0,\r\n                       ${this.pixelSize * 2}px ${this.pixelSize * -2}px 0 0,\r\n                       ${this.pixelSize * -2}px ${this.pixelSize * 2}px 0 0,\r\n                       0 ${this.pixelSize}px 0 0,\r\n                       ${this.pixelSize}px 0 0 0,\r\n                       ${this.pixelSize * -1}px 0 0 0,\r\n                       0 ${this.pixelSize * -1}px 0 0;\r\n        }\r\n\r\n        100% {\r\n          transform: rotate(360deg);\r\n        }`\r\n      }\r\n    }\r\n  }\r\n</script>\r\n\r\n<style scoped>\r\n  .pixel-spinner, .pixel-spinner * {\r\n    box-sizing: border-box;\r\n  }\r\n\r\n  .pixel-spinner {\r\n    height: 70px;\r\n    width: 70px;\r\n    display: flex;\r\n    flex-direction: row;\r\n    justify-content: center;\r\n    align-items: center;\r\n  }\r\n\r\n  .pixel-spinner .pixel-spinner-inner {\r\n    width: calc(70px / 7);\r\n    height: calc(70px / 7);\r\n    background-color: #ff1d5e;\r\n    color: #ff1d5e;\r\n    box-shadow: 15px 15px  0 0,\r\n    -15px -15px  0 0,\r\n    15px -15px  0 0,\r\n    -15px 15px  0 0,\r\n    0 15px  0 0,\r\n    15px 0  0 0,\r\n    -15px 0  0 0,\r\n    0 -15px 0 0;\r\n    animation: pixel-spinner-animation 2000ms linear infinite;\r\n  }\r\n\r\n  /* NOTE Keyframes here serve as reference. They don't do anything. */\r\n  @keyframes pixel-spinner-animation {\r\n    50% {\r\n      box-shadow: 20px 20px 0px 0px,\r\n      -20px -20px 0px 0px,\r\n      20px -20px 0px 0px,\r\n      -20px 20px 0px 0px,\r\n      0px 10px 0px 0px,\r\n      10px 0px 0px 0px,\r\n      -10px 0px 0px 0px,\r\n      0px -10px 0px 0px;\r\n    }\r\n    75% {\r\n      box-shadow: 20px 20px 0px 0px,\r\n      -20px -20px 0px 0px,\r\n      20px -20px 0px 0px,\r\n      -20px 20px 0px 0px,\r\n      0px 10px 0px 0px,\r\n      10px 0px 0px 0px,\r\n      -10px 0px 0px 0px,\r\n      0px -10px 0px 0px;\r\n    }\r\n    100% {\r\n      transform: rotate(360deg);\r\n    }\r\n  }\r\n</style>\r\n"],"sourceRoot":""}]);
